.theme-night {
  color-scheme: dark;
}
* {
  padding: 0;
  border: 0;
  border-radius: 0;
  outline: 0;
  -webkit-tap-highlight-color: transparent;
}
body {
  font-family: 'YS Display', sans-serif;
  font-family: var(--font-family);
  font-size: 16px;
  font-size: var(--size_m);
  color: #002033;
  color: var(--text-primary);
  background: #f7f8fb;
  background: var(--bg-accent);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -moz-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
  text-size-adjust: 100%;
}
[lang='sjn'] {
  font-family: 'Tengwar Annatar', Arial, Helvetica, sans-serif;
}
[lang='am'] {
  font-family: 'GF Zemen Unicode', Arial, Helvetica, sans-serif;
}
[lang='my'] {
  font-family: 'Noto Sans Myanmar', Arial, Helvetica, sans-serif;
}
*,
body,
pre,
ul {
  margin: 0;
}
pre {
  font-family: 'YS Display', sans-serif;
  font-family: var(--font-family);
}
ul {
  padding: 0;
  list-style: none;
}
a {
  cursor: pointer;
  text-decoration: none;
  color: #1983ff;
  color: var(--text-link);
  transition: color 0.2s ease;
}
a:active,
a:hover {
  color: #3994ff;
  color: var(--text-link-hover);
}
button,
textarea {
  font: inherit;
  background: 0 0;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
}
button {
  text-align: left;
  color: inherit;
  -webkit-font-smoothing: inherit;
  -moz-osx-font-smoothing: inherit;
}
textarea {
  display: block;
  box-sizing: border-box;
  width: 100%;
  resize: none;
  border: 0;
}
textarea::-webkit-input-placeholder {
  color: rgba(0, 32, 51, 0.4);
  color: var(--control-placeholder);
}
textarea::-moz-placeholder {
  color: rgba(0, 32, 51, 0.4);
  color: var(--control-placeholder);
}
textarea:-ms-input-placeholder {
  color: rgba(0, 32, 51, 0.4);
  color: var(--control-placeholder);
}
textarea::placeholder {
  color: rgba(0, 32, 51, 0.4);
  color: var(--control-placeholder);
}
textarea:focus {
  outline: 0;
}
[contenteditable],
input {
  outline: 0;
}
[data-inner-text]::before {
  content: attr(data-inner-text);
}
.state-tab-focused {
  outline: rgba(0, 32, 51, 0.4) 1px dotted;
  outline: var(--control-placeholder) 1px dotted;
}
.separator {
  height: 1px;
  margin: 8px 0;
  background-color: rgba(0, 42, 104, 0.08);
  background-color: var(--bg-border);
}

.closeButton:active,
.closeButton:hover {
  opacity: 1;
}
.YndxBug:not([class*='Bug__container_expanded']) > div {
  border-width: 1px 0 0 1px;
  border-color: rgba(0, 42, 104, 0.08);
  border-color: var(--bg-border);
  border-radius: 8px 0 0;
  background-color: #fff;
  background-color: var(--bg-default);
}
:root {
  --page-max-width: 1100px;
  --page-max-wide-width: 1800px;
  --page-offset: 16px;
  --page-offset-tablet: 36px;
  --page-offset-text: 64px;
}
.page,
body,
html {
  height: 100%;
  min-height: 100%;
}
.page {
  display: flex;
  flex-direction: column;
  max-width: 1100px;
  max-width: var(--page-max-width);
  margin: auto;
  padding: 0 16px;
  padding: 0 var(--page-offset);
}
.page.page_text {
  padding: 0 64px;
  padding: 0 var(--page-offset-text);
}
.page.page_wide {
  max-width: 1800px;
  max-width: var(--page-max-wide-width);
}
.page.page_full {
  max-width: 100%;
  padding: 0;
}
.page.page_with-sticky-header {
  margin-top: 74px;
}
.footer,
.header,
.sticky-header .header-nav-btn svg {
  flex-shrink: 0;
}
.page-box_full {
  margin: 0 24px;
  margin: 0 var(--space_xl);
}
.page-content {
  flex: 1 0 auto;
  min-height: 290px;
}
@media (max-width: 900px) {
  .page,
  .page.page_text {
    padding: 0 36px;
    padding: 0 var(--page-offset-tablet);
  }
}
.header {
  display: flex;
  align-items: center;
  padding: 17px 0;
}
.page_wide .header {
  padding: 17px 24px;
  padding: 17px var(--space_xl);
}

